<template>
<div>
    <p>userinfo</p>
    <!--<p>{{authUser.access_token}}</p>-->
  <router-link to="Login"><el-button>Login</el-button></router-link>
  <p>Full Name: {{user.full_name}}</p>
  <p>Email: {{user.email}}</p>
</div>

</template>
<script>
    import ls from 'localStorage'
    import ElButton from "../../node_modules/element-ui/packages/button/src/button.vue";

    export default {
      components: {ElButton},
      props:{

        },
        data(){
            let user = JSON.parse(ls.getItem("userInfo"));
            if(!user){
              user = {
                full_name: 'No user found',
                email: 'No user found'
              }
            }
            console.log(ls.getItem("userInfo"));
            console.log('parse authUser',user)
            return {user};
        }
    }
</script>



